import {Component} from '@angular/core';
import {NavController, ToastController, MenuController, PopoverController, ModalController} from 'ionic-angular';
import {AddaccountPage} from "../addaccount/addaccount";
import Neb from 'nebulas';
import * as nebu from 'nebulas';
import NebPay from 'nebpay';
import {AddInfoComponent} from "../../components/add-info/add-info";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  toAddr = 'n1yAgxfX4uFbwyZKpMckdxccmNHMNMdiX1X';
  nas
  account;
  title = "密码保险箱";

  constructor(public navCtrl: NavController, public toast: ToastController,
              public menu: MenuController, public pop: PopoverController, public modal: ModalController) {
    this.getWallectInfo();
  }

  inputItems = [
    {title: "数字货币", one: "标题", two: "钱包地址", three: "钱包密码", four: "备注", type: 1,icon:"assets/icon/icon_1.png"},
    {title: "银行卡号", one: "标题", two: "银行卡号", three: "银行卡密码", four: "备注", type: 2,icon:"assets/icon/icon_2.png"},
    {title: "电子邮件", one: "标题", two: "邮箱帐号", three: "邮箱密码", four: "备注", type: 3,icon:"assets/icon/icon_3.png"},
    {title: "QQ帐号", one: "标题", two: "QQ帐号", three: "QQ密码", four: "备注", type: 4,icon:"assets/icon/icon_4.png"},
    {title: "微信帐号", one: "标题", two: "微信帐号", three: "微信密码", four: "备注", type: 5,icon:"assets/icon/icon_5.png"},
    {title: "微博帐号", one: "标题", two: "微博帐号", three: "微博密码", four: "备注", type: 6,icon:"assets/icon/icon_6.png"},
    {title: "电脑/Pad", one: "标题", two: "用户名", three: "密码", four: "备注", type: 7,icon:"assets/icon/icon_7.png"},
    {title: "会员卡", one: "标题", two: "卡号", three: "密码", four: "备注", type: 8,icon:"assets/icon/icon_8.png"},
    {title: "备忘", one: "标题", two: "", three: "", four: "备注", type: 9,icon:"assets/icon/icon_9.png"}
  ]
  menuItems = [
    {name: "数字货币", type: 1,icon:"assets/icon/icon_1.png"},
    {name: "银行卡号", type: 2,icon:"assets/icon/icon_2.png"},
    {name: "电子邮件", type: 3,icon:"assets/icon/icon_3.png"},
    {name: "QQ帐号", type: 4,icon:"assets/icon/icon_4.png"},
    {name: "微信帐号", type: 5,icon:"assets/icon/icon_5.png"},
    {name: "微博帐号", type: 6,icon:"assets/icon/icon_6.png"},
    {name: "电脑/Pad密码", type: 7,icon:"assets/icon/icon_7.png"},
    {name: "会员卡号", type: 8,icon:"assets/icon/icon_8.png"},
    {name: "备忘", type: 9,icon:"assets/icon/icon_9.png"},
  ]
  itemType = -1;

  onItemClick(item) {
    this.itemType = item.type;
    this.title = item.name;
    this.menu.toggle()
  }

  goToAddAccountPage() {
    this.modal.create(AddInfoComponent, {title: "添加" + this.title, type: this.itemType}).present()
    // this.navCtrl.push(AddaccountPage)
  }

  getData() {
    var nebulas = require("nebulas"),
      Account = nebulas.Account,
      neb = new nebu.Neb();
    neb.setRequest(new nebulas.HttpRequest("https://mainnet.nebulas.io"));
    var value = "0";
    var nonce = "0"
    var gas_price = "1000000"
    var gas_limit = "2000000"
    var callFunction = "get";
    var callArgs = ""
    var contract = {
      "function": callFunction,
      "args": callArgs
    }

    neb.api.call(this.account, this.toAddr, value, nonce, gas_price, gas_limit, contract)
      .then((resp) => {
        this.cbPush(resp)
      }).catch(function (err) {
      console.log(err);
    })
  }

  items;

  cbPush(resp) {
    console.log(resp)
    this.items = JSON.parse(resp.result);

  }

  getWallectInfo() {
    window.postMessage({
      "target": "contentscript",
      "data": {},
      "method": "getAccount",
    }, "*");
    window.addEventListener('message', (e) => {
      if (e.data && e.data.data) {
        if (e.data.data.account) {//这就是当前钱包中的地址
          this.account = e.data.data.account
          this.getData();
        }
      }
    });
  }

  editItem(item) {
    this.modal.create(AddInfoComponent,
      {title: "修改" + item.type_name, type: item.type,mode:1,one:item.one,two:item.two,three:item.three,four:item.four,uid:item.uid}).present()//mode 0添加 1修改

  }

  deleteItem(item) {
    var ebPay = new NebPay();
    var value = "0";
    var callFunction = "delete"
    var arr = [];
    arr.push(item.uid);
    var serialNumber = nebPay.call(this.toAddr, value, callFunction, JSON.stringify(arr), {    //使用nebpay的call接口去调用合约,
      listener: this.callback
    });
    this.removeItem(item);
  }

  callback(resp) {
    console.log(resp)

  }

  removeItem(item) {
    for (var i = 0; i < this.items.length; i++) {
      if (item.uid == this.items[i].uid) {
        this.items.splice(i, 1);
      }
    }
  }
}



