import { Component } from '@angular/core';
import {NavParams,NavController,ToastController,LoadingController,ModalController} from "ionic-angular";
import NebPay from 'nebpay';
@Component({
  selector: 'add-info',
  templateUrl: 'add-info.html'
})
export class AddInfoComponent {
   toAddr = 'n1yAgxfX4uFbwyZKpMckdxccmNHMNMdiX1X'
  title: string;
  type;
  item;
  mode =0;
  uid;
  inputItems = [
    {title:"数字货币",one:"标题",two:"钱包地址",three:"钱包密码",four:"备注",type:1},
    {title:"银行卡号",one:"标题",two:"银行卡号",three:"银行卡密码",four:"备注",type:2},
    {title:"电子邮件",one:"标题",two:"邮箱帐号",three:"邮箱密码",four:"备注",type:3},
    {title:"QQ帐号",one:"标题",two:"QQ帐号",three:"QQ密码",four:"备注",type:4},
    {title:"微信帐号",one:"标题",two:"微信帐号",three:"微信密码",four:"备注",type:5},
    {title:"微博帐号",one:"标题",two:"微博帐号",three:"微博密码",four:"备注",type:6},
    {title:"电脑/Pad",one:"标题",two:"用户名",three:"密码",four:"备注",type:7},
    {title:"会员卡",one:"标题",two:"卡号",three:"密码",four:"备注",type:8},
    {title:"备忘",one:"标题",two:"",three:"",four:"备注",type:9},
  ]
  constructor(params: NavParams,public ctrl:NavController,public toast:ToastController,public loadingCtrl:LoadingController,public modal:ModalController) {
    this.title =  params.get('title');
    this.type = params.get("type")-1;
    this.mode = params.get("mode");
    this.item = this.inputItems[this.type]
    if(this.mode==1){
      this.one = params.get("one");
      this.two = params.get("two");
      this.three = params.get("three");
      this.four = params.get("four");
      this.uid = params.get("uid");
    }

  }
  one;
  two;
  three;
  four;
  add(){
    if(this.mode == 1){//添加
      if(this.type!=8){//不是备忘
        if(this.one==null){
          this.showToast(this.item.one+"不能为空");
          return ;
        }
        if(this.two==null){
          this.showToast(this.item.two+"不能为空");
          return ;
        }
      }else{

        if(this.one==null){
          this.showToast(this.item.one+"不能为空");
          return ;
        }
        if(this.four==null){
          this.showToast(this.item.four+"不能为空");
          return ;
        }
      }
      this.updateToNas();
      this.ctrl.pop()
      // this.presentLoadingCustom();
    }else{//修改

      if(this.type!=8){//不是备忘
        if(this.one==null){
          this.showToast(this.item.one+"不能为空");
          return ;
        }
        if(this.two==null){
          this.showToast(this.item.two+"不能为空");
          return ;
        }
      }else{

        if(this.one==null){
          this.showToast(this.item.one+"不能为空");
          return ;
        }
        if(this.four==null){
          this.showToast(this.item.four+"不能为空");
          return ;
        }
      }
      this.saveToNas();
      this.ctrl.pop()
    }


  }
  showToast(msg){
    this.toast.create({
      message:msg,
      duration:3000,
      showCloseButton:true,
      position:"middle",
      closeButtonText:"关闭"
    }).present()
  }
  updateToNas(){
    var nebPay = new NebPay();
    var value = "0";
    var callFunction = "set"
    var arr = [];
    arr.push(this.one);
    arr.push(this.two);
    arr.push(this.three);
    arr.push(this.four);
    arr.push(this.uid);
    var serialNumber = nebPay.call(this.toAddr, value, callFunction, JSON.stringify(arr), {    //使用nebpay的call接口去调用合约,
      listener: this.callback        //设置listener, 处理交易返回信息
    });
  }
  saveToNas(){
    var nebPay = new NebPay();
    var value = "0";
    var callFunction = "set"
    var arr = [];
    arr.push(this.one);
    arr.push(this.two);
    arr.push(this.three);
    arr.push(this.four);
    arr.push(this.type+1);
    arr.push(new Date().getTime());
    arr.push(this.item.title);
    var serialNumber = nebPay.call(this.toAddr, value, callFunction, JSON.stringify(arr), {    //使用nebpay的call接口去调用合约,
      listener: this.callback        //设置listener, 处理交易返回信息
    });
  }
  callback(resp) {
    console.log(resp)
  }

  presentLoadingCustom() {
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `
      <div class="custom-spinner-container">
        <div class="custom-spinner-box"></div>
      </div>`,
      duration: 5000
    });

    loading.onDidDismiss(() => {
      console.log('Dismissed loading');
    });

    loading.present();
  }
}



